import { Component, Input } from '@angular/core';
import { RichTextEditorActionsService } from './rich-text-editor-actions.service';

export type CommandType =
  | 'bold'
  | 'formatBlock'
  | 'insertImage'
  | 'insertHTML'
  | 'italic'
  | 'createLink'
  | 'insertOrderedList'
  | 'strikeThrough'
  | 'insertUnorderedList'
  | 'underline';

@Component({
  template: '',
})
export class RichTextEditorActionsComponent {
  /**
   * This Boolean attribute indicates that the user cannot interact with the button.  Defaults to
   * false.
   */
  @Input() disabled = false;
  /**
   * Placement of the action tooltip relative to the action button: auto | top | bottom | left | right.
   */
  @Input() actionsTooltipPlacement = 'auto';
  constructor(private readonly ma: RichTextEditorActionsService) {}

  result(command: CommandType, value?: string | undefined) {
    this.ma.exec(command, value);
  }
}
