import { Component } from '@angular/core';
import { RichTextEditorActionsService } from '../rich-text-editor-actions.service';
import { RichTextEditorActionsComponent } from '../rich-text-editor-actions.component';

@Component({
  selector: 'bb-rich-text-editor-image-action',
  templateUrl: './rich-text-editor-image-action.component.html',
})
export class RichTextEditorImageActionComponent extends RichTextEditorActionsComponent {
  constructor(ma: RichTextEditorActionsService) {
    super(ma);
  }

  onResult() {
    const url = window.prompt('Enter the image URL');
    if (url) super.result('insertImage', url);
  }
}
