import {
  ChangeDetectorRef,
  Component,
  ContentChild,
  Directive,
  ElementRef,
  forwardRef,
  HostBinding,
  HostListener,
  Inject,
  Input,
  Renderer2,
  SecurityContext,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import {
  AbstractControl,
  ControlValueAccessor,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  ValidationErrors,
  Validator,
  Validators,
} from '@angular/forms';
import { InputBaseComponent } from '@backbase/ui-ang/base-classes';
import { browserIsMsie, getKeyCode, KEY_CODES } from '@backbase/ui-ang/util';
import { DomAttributesService } from '@backbase/ui-ang/services';
import { DeprecationsService } from '@backbase/foundation-ang/future';
import { DomSanitizer } from '@angular/platform-browser';
import { DOCUMENT } from '@angular/common';

@Directive({ selector: 'ng-template[bbRichTextEditorActions]' })
export class RichTextEditorActionsDirective {
  constructor(public templateRef: TemplateRef<any>) {}
}

/**
 * @name RichTextEditorComponent
 *
 * @description
 * Component that provides you input text with editing.
 *
 * @dynamic (to suppress error with resolving Document type during compilation)
 */
@Component({
  selector: 'bb-rich-text-editor-ui',
  templateUrl: './rich-text-editor.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RichTextEditorComponent),
      multi: true,
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => RichTextEditorComponent),
      multi: true,
    },
  ],
})
export class RichTextEditorComponent extends InputBaseComponent implements ControlValueAccessor, Validator {
  /**
   * The maxLength for the rich-text-editor component.
   */
  @Input() maxLength = Infinity;
  /**
   * The minLength for the rich-text-editor component.
   */
  @Input() minLength = 0;
  /**
   * The placeholder for the rich-text-editor component. Defaults to an empty string;
   */
  @Input() placeholder = '';
  /**
   * Show/hide message length. Defaults to true;
   */
  @Input() counter = true;
  /**
   * Whether the rich-text-editor component is readonly.
   */
  @Input() readonly = false;
  /**
   * Placement of the action tooltip relative to the action button: auto | top | bottom | left | right.
   */
  @Input() actionsTooltipPlacement = 'auto';
  @ContentChild(RichTextEditorActionsDirective, { static: true }) actions: RichTextEditorActionsDirective | undefined;
  @ViewChild('contenteditable', { static: true }) contentEditable: ElementRef | undefined;
  @HostBinding('class.is-focused') focused = false;

  readonly hostRef: RichTextEditorComponent = this;

  public readonly labelId: string;
  public readonly contentClassName = 'bb-rich-text-editor__content';
  public readonly mainAreaClassName = 'bb-rich-text-editor__mainarea';
  private range?: Range;

  value = '';

  constructor(
    protected readonly cd: ChangeDetectorRef,
    private domAttributesService: DomAttributesService,
    protected readonly deprecationsService: DeprecationsService,
    private readonly sanitizer: DomSanitizer,
    private readonly renderer: Renderer2,
    @Inject(DOCUMENT) private readonly document: Document,
  ) {
    super(cd, deprecationsService);
    this.labelId = this.domAttributesService.generateId();
  }

  @HostListener('keydown', ['$event'])
  @HostListener('keyup', ['$event'])
  onKeyEvent($event: KeyboardEvent) {
    if (this.contentEditable && browserIsMsie()) {
      const action = getKeyCode($event);

      if (($event.ctrlKey || $event.metaKey) && action === KEY_CODES.X) {
        this.callOnChange();
      }

      if (action === KEY_CODES.BACKSPACE || action === KEY_CODES.DELETE) {
        this.callOnChange();
      }
    }
  }

  @HostListener('paste')
  onPaste() {
    if (this.contentEditable && browserIsMsie()) {
      this.callOnChange();
    }
  }

  @HostListener('mouseup', ['$event'])
  onClickEvent($event: Event) {
    if (this.document.getSelection()?.rangeCount) {
      this.range = this.document.getSelection()?.getRangeAt(0);
    }

    if (($event?.target as HTMLElement).closest('button') && this.range?.endOffset === this.range?.startOffset) {
      this.restoreCaretPosition(this.contentEditableElement);
      this.callOnChange();
    }
  }

  writeValue(inputValue: string | null) {
    if (this.contentEditable) {
      const finalValue = this.parseValue(inputValue || '');

      this.renderer.setProperty(this.contentEditable.nativeElement, 'innerHTML', finalValue);
      super.onValueChange(finalValue);
    }
    this.cd.markForCheck();
  }

  callOnChange() {
    const finalValue = (this.contentEditable && this.parseValue(this.contentEditable.nativeElement.innerHTML)) || '';

    super.onValueChange(finalValue);
  }

  validate(control?: AbstractControl): ValidationErrors | null {
    if (control) {
      console.warn(
        '[bb-rich-text-editor-ui]: "control" argument in "validate" method is deprecated and will be removed ' +
          'in the next major release',
      );
    }
    const value = this.getMessageText() || null;
    const validators = [Validators.maxLength(this.maxLength), Validators.minLength(this.minLength)];

    if (this.required) {
      validators.push(this.customRequiredValidation.bind(this));
    }

    const compose = Validators.compose(validators);

    return compose && compose({ value } as AbstractControl);
  }

  labelClick() {
    if (this.contentEditable) {
      this.contentEditable.nativeElement.focus();
    }
  }

  callOnFocus() {
    super.onFocus();
    this.focused = true;
  }

  callOnBlur() {
    setTimeout(() => {
      super.onBlur();
      this.focused = false;
      this.cd.markForCheck();
    });
  }

  getMessageText(content?: string): string {
    if (content) {
      console.warn(
        '[bb-rich-text-editor-ui]: "content" argument in "getMessageText" method is deprecated and will ' +
          'be removed in the next major release',
      );
    }

    return this.contentEditableElement.textContent || '';
  }

  showCounter(): boolean {
    return this.counter && !!this.maxLength && Number.isFinite(this.maxLength);
  }

  linkAdded() {
    this.callOnChange();
    this.restoreCaretPosition(this.contentEditableElement);
  }

  private get contentEditableElement(): HTMLElement {
    return this.contentEditable?.nativeElement as HTMLElement;
  }

  private parseValue(value: string): string {
    const isHtml = /<\/?[a-z][\s\S]*>/i.test(value);

    return this.replaceSpaces(isHtml ? this.sanitizer.sanitize(SecurityContext.HTML, value) || '' : value);
  }

  private readonly replaceSpaces = (value: string): string => value.replace(/&nbsp;|&#160;/g, ' ');

  private customRequiredValidation(): ValidationErrors | null {
    const value = this.getMessageText();

    if (!value || (typeof value === 'string' && !value.trim())) {
      return {
        required: true,
      };
    }

    return null;
  }

  private restoreCaretPosition(el: HTMLElement) {
    const emptyRange = document.createRange();
    const selection = window.getSelection();
    const isEditorSelection = !!this.range?.endContainer.parentElement?.closest(`.${this.contentClassName}`);
    emptyRange.selectNodeContents(el);
    emptyRange.collapse(false);
    selection?.removeAllRanges();
    if (isEditorSelection) {
      selection?.addRange(this.range as Range);
    } else {
      selection?.addRange(emptyRange);
    }
  }
}
