import { Component, EventEmitter, Inject, Input, Output } from '@angular/core';
import { RichTextEditorActionsService } from '../rich-text-editor-actions.service';
import { RichTextEditorActionsComponent } from '../rich-text-editor-actions.component';
import { DOCUMENT } from '@angular/common';

/**
 * @name RichTextEditorLinkActionComponent
 *
 * @dynamic (to suppress error with resolving statics declarations during compilation)
 */

@Component({
  selector: 'bb-rich-text-editor-link-action',
  templateUrl: './rich-text-editor-link-action.component.html',
})
export class RichTextEditorLinkActionComponent extends RichTextEditorActionsComponent {
  /**
   * Event emitted after link was added
   */
  @Output() linkAdded = new EventEmitter();

  /**
   * List of classes to reach out editor's content
   */
  @Input() contentClassNames!: string[];

  /**
   * Flag to determine whether the add link modal is open
   */
  modalOpened = false;

  /**
   * Selection link url
   */
  selectionUrl = '';

  private selection?: Selection | null;
  private range?: Range;

  constructor(ma: RichTextEditorActionsService, @Inject(DOCUMENT) private readonly document: Document) {
    super(ma);
  }

  /**
   * Open add link modal
   */
  openModal() {
    this.modalOpened = true;
    this.setSelectionData();
    if (this.selection?.rangeCount && this.selection?.rangeCount > 0) {
      this.selectionUrl =
        this.linkedSelection?.nodeName === 'A' && this.linkedSelection?.href !== ''
          ? this.linkedSelection.href
          : 'http://';
    }
  }

  /**
   * On add link modal confirmation
   */
  onResult() {
    this.restoreSelection();
    this.selectionUrl = this.selectionUrl.replace(/\s/g, '');
    if (this.selectionUrl.length === 0) {
      this.document.execCommand('unlink', false);
    } else {
      this.insertLink();
    }

    this.closeModal();
  }

  /**
   * Close add link modal
   */
  closeModal() {
    this.modalOpened = false;
    this.linkAdded.emit();
  }

  private setSelectionData() {
    this.selection = this.document.getSelection();
    this.range = this.selection?.getRangeAt(0);
    this.selectionUrl = '';
  }

  private restoreSelection() {
    if (this.range) {
      this.selection?.removeAllRanges();
      this.selection?.addRange(this.range);
    }
  }

  private insertLink() {
    if (!/^(http:\/\/|https:\/\/)/.test(this.selectionUrl)) {
      this.selectionUrl = 'http://' + this.selectionUrl;
    }

    if (this.range?.toString() !== '') {
      this.document.execCommand('createLink', false, this.selectionUrl);
    } else {
      this.document.execCommand('insertHTML', false, this.linkElement);
    }
  }

  private get linkElement(): string {
    const innerText = this.selection?.toString() || this.selectionUrl;
    const element = this.document.createElement('a');
    element.setAttribute('href', this.selectionUrl);
    element.setAttribute('target', '_blank');
    element.innerHTML = innerText;

    return element.outerHTML;
  }

  private get linkedSelection(): HTMLAnchorElement | null {
    return (
      this.range?.commonAncestorContainer?.parentElement?.closest('a') ||
      (this.range?.commonAncestorContainer as HTMLElement)?.children?.[0]?.closest('a')
    );
  }
}
